import Container from "../components/container"
import * as React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import { Link } from "gatsby"
import Header from "../components/header"
import Footer from "../components/footer"

export default function Collections({data}) {
  return (
    <Container>
      <Header headerTitle="Collections" headerDescription="Italian based web developer passionate about photography"></Header>
      <section className="hero">
        <div className="container">
          <div className="hero-body">
            <p className="title">
              collections
            </p>
            <p className="subtitle">
              my photographic journey
            </p>
          </div>
        </div>
      </section>
      <section id="cp_categories" className="section">
        <div className="container">
          <div className="content">
            <div className="columns is-multiline">
              {
                data.allMarkdownRemark.edges.map(
                  ({node}) => (
                    <div className="column is-half cp_category_element" key={node.id}>
                      <div className="card">
                        <div className="card-image">
                          <Img fluid={node.frontmatter.featuredImage.childImageSharp.fluid} />
                        </div>
                        <div className="card-content">
                          <div className="content">
                            <p className="title is-4">{node.frontmatter.title}</p>
                            <p className="subtitle is-6">
                              <span className="is-size-6" style={{ color: `#999` }}>
                                <span className={`${node.frontmatter.type !== 'primary' ? 'hide' : ''}`} >Last update: </span>
                                <span className={`${node.frontmatter.type === 'primary' ? 'hide' : ''}`} >Published on: </span>
                                {node.frontmatter.date}
                              </span>
                            </p>
                            <p>
                              {node.frontmatter.excerpt}
                            </p>
                            <Link to={node.fields.slug}>
                              View more &#8250;
                            </Link>
                          </div>
                        </div>
                      </div>
                    </div>
                  )
                )
              }
            </div>
          </div>
        </div>
      </section>
      <Footer></Footer>
    </Container>
  )
}

export const query = graphql`
  {
    allMarkdownRemark(sort: {order: DESC, fields: frontmatter___date}) {
      edges {
        node {
          frontmatter {
            title
            date
            type
            excerpt
            featuredImage {
              childImageSharp {
                fluid(maxWidth: 540, maxHeight: 340) {
                  aspectRatio
                  base64
                  originalImg
                  originalName
                  presentationHeight
                  presentationWidth
                  sizes
                  src
                  srcSet
                  srcSetWebp
                  srcWebp
                }
              }
            }
          }
          fields {
            slug
          }
          id
        }
      }
      totalCount
    }
  }
`
